import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Message } from 'primeng/api/message';

import uuidv4 from 'uuid/dist/v4';
import { DialogService, DynamicDialogRef } from 'primeng/dynamicdialog';
import { LoginFormComponent } from 'src/app/core/components/login-form/login-form.component';
import { RoomConfigFormComponent } from 'src/app/core/components/room-config-form/room-config-form.component';
@Component({
  selector: 'back-office-videocall',
  templateUrl: './videocall.component.html',
  styleUrls: ['./videocall.component.scss'],
  providers: [DialogService]
})
export class VideoCallComponent implements OnInit {
  code: any;
  codeInput: string;
  public joincallFormGroup: FormGroup;
  public messages: Message[];

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    public dialogService: DialogService
  ) {}

  ref: DynamicDialogRef;

  ngOnInit(): void {
    console.log('ngOnInit');
    this.joincallFormGroup = this.fb.group({
      codeInput: ['', [Validators.required]]
    });
    if (this.route.snapshot.params.id !== '') {
      this.code = this.route.snapshot.params.id;
      this.codeInput = this.code;
    }
  }

  /**
   * Configure a new videochat room
   */
  showRoomConfiguration(mode: string): void {
    console.log('showRoomConfiguration');
    this.ref = this.dialogService.open(RoomConfigFormComponent, {
      data: {
        mode
      },
      header: mode === 'create' ? 'Create room' : 'Join room',
      width: '35%'
    });
  }

  /**
   * Create a new videochat room
   */
  createRoom(): void {
    console.log('createRoom');
    this.router.navigate([`/video-call/${uuidv4()}`]);
  }

  /**
   * Join a videochat room
   */
  joinRoom(): void {
    console.log('joinRoom');
    if (this.joincallFormGroup.valid) {
      console.log(this.code);
      if (this.code) {
      }
    }
  }
}
